import * as type from '../types';

const defaultState = () => ({
    isLoading: false,
    data: [],
    status: false
})

const signupReducer = (state, action) => {
    if (state === undefined) {
        return defaultState();
    }
    switch(action.type) {
        case type.SIGN_UP: 
            return {
                isLoading: true,
                data: [...state.data],
                status: true
            } 
        case type.SIGNUP_SUCCESS: 
            return {
                isLoading: false,
                data: [...state.data, action.payload],
                status: false
            } 
        case type.SIGNUP_FAIL: 
            return {
                isLoading: false,
                data: [...state.data],
                status: false
            } 
        default: 
            return state;
    }
}
export default signupReducer;