import React from 'react';
import {connect} from 'react-redux';
import { Link } from 'react-router-dom';
import {addSubmit, addTodo, dropSubmit, remove} from '../../actions';
import classes from './home.module.css';

class Home extends React.Component {
    // constructor(props) {
    //     super(props);
    //     // console.log('count data', this.props.countData);
    // }

    componentDidUpdate(prevProps) {
        //check previous state
        const{todoData} = this.props;

        // check if todoData.isLoading == false && todoData.isLoading == true
        if(prevProps.todoData.isLoading && !todoData.isLoading) {
            console.log('Data is updated!');
            // alert("The data has been updated");
        }
        if(prevProps.todoData.delLoading && !todoData.delLoading) {
            console.log('Data is deleted!');
            // alert("The data has been updated");delLoading
        }
    }

    addCount() {
        this.props.addSubmit(2);
    }
    dropCount() {
        this.props.dropSubmit(-2);
    }

    addTodo() {
        const inputVal = this._inputEle.value;
        if(inputVal !== "") {
            const data = {
                title: inputVal,
                key: Date.now(),
            }
            this.props.addTodo(data);
            this._inputEle.value = '';
        } else {
            alert('No data has been inserted');
        }
        
    }

    removeList(key) {
        // console.log(key);
        this.props.removeData(key);
    }

    render() {
        return(
            <div className={classes.main}>
                <button className={classes.button}>
                    <Link className={classes.link} to="/">Log out</Link>
                </button>
                <h1>This is home</h1>
                <h1>Count: {this.props.countData}</h1>
                <div>
                    <button className={classes.button} onClick={() => this.addCount()}>Plus 2</button>
                    <button className={classes.button} onClick={() => this.dropCount()}>Minus 2</button>
                </div>
                <input className={classes.input} type="text" placeholder="Add to do" ref={(ele) => this._inputEle = ele } />
                {
                    this.props.todoData.isLoading ? (
                        <p>Loading...</p>
                    ) : (
                        <button className={classes.button} onClick={() => this.addTodo()}>Add</button>
                    )
                }
                
                {/* if we use {} its like we're calling js instead of passing parameter */}
                <div>
                    <ul className={classes.container}>
                        {this.props.todoData.data.map((list) => (
                            <div className={classes.listContainer}>
                                <li className={classes.list} >{list.title}</li>
                                {
                                    this.props.todoData.delLoading ? (
                                        <p>Deleting...</p>
                                    ) : (
                                        <div >
                                            {/* <li style={styles.list}>{list.title}</li> */}
                                            <button onClick={()=>this.removeList(list.key)}>Remove</button>
                                        </div>
                                    )
                                }
                                {/* <li style={styles.list}>{list.title}</li>
                                <button onClick={()=>this.removeList(list.key)}>Remove</button> */}
                            </div>
                        ))}
                    </ul>
                </div> 
            </div>
        );
    }
}

//getting the state from the store
const mapStateToProps = (state) => ({
    countData: state.count,
    todoData: state.todo,
    removeData: state.remove,
});
const mapDispatchToProps = {
    addSubmit: addSubmit,
    dropSubmit: dropSubmit,
    addTodo: addTodo,
    removeData: remove,
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);