import * as type from '../types';

const defaultState = () => ({
    isLoading: false,
    data: [],
    error: null,
    status: false
})

const loginReducer = (state, action) => {
    if (state === undefined) {
        return defaultState();
    }
    switch(action.type) {
        case type.LOGIN: 
            return {
                isLoading: true,
                data: [...state.data],
                error: null,
                status: true
            } 
        case type.LOGIN_SUCCESS: 
            return {
                isLoading:false,
                data: [...state.data, action.payload],
                error: null,
                status: false
            } 
        case type.LOGIN_FAIL: 
            return {
                isLoading:false,
                data: [...state.data],
                error: action.error,
                status: false
            } 
        default: 
            return state;
    }
}
export default loginReducer;