import React from 'react';
import classes from './login.module.css'; 
import {connect} from 'react-redux';
import {login} from '../../actions';

import {Link, Redirect} from 'react-router-dom';

class Login extends React.Component {
    login() {
        const useremail = this._loginemail.value;
        const userpassword = this._loginpassword.value;
        if(useremail !== "" || userpassword !== "") {
            this.props.signupData.data.map((check)=> {
                console.log(check)    ;
                // console.log('hello');
                // const data = {
                //     email: useremail,
                //     pw: userpassword
                // }
                // this.props.login(data);
                // // this._loginemail.value = '';
                // // this._loginpassword.value = '';
                // window.location.href = "http://localhost:3000/home";
                if(useremail === check.email && userpassword === check.password){
                    const data = {
                        email: useremail,
                        pw: userpassword,
                    }
                    this.props.login(data);
                    // this._loginemail.value = '';
                    // this._loginpassword.value = '';
                    // window.location.href = "http://localhost:3000/home";
                } 
                // else {
                //     alert('Your credentials dont match');
                // }
            })
        } else {
            alert('Please fill in all the details');
        }
    }

    render() {
        return(
            <div className={classes.container}>
                <div className={classes.login}>
                {this.props.loginData.status == true ? <Redirect to="/home" /> : null}
                    <h1 style={{color:`#154c79`}}>Log In</h1>
                    <p>Please fill in the details to view the dashboard..</p>
                    <input className={classes.input} type="text" id="email" placeholder="email" ref={(a)=> this._loginemail = a} />
                    <input className={classes.input} type="text" id="password" placeholder="password" ref={(a)=> this._loginpassword = a} />
                    <div className={classes.button_holder}>
                    <button className={classes.btn} onClick={()=> this.login()}>Login</button>
                        {/* {
                            this.props.signupData.isLoading ? (
                                <p>Directing to Dashboard...</p>
                            ) : (
                                <button className={classes.btn} onClick={()=> this.login()}>Login</button>
                            )
                        } */}
                        
                        <button className={classes.btn} >
                            <Link className={classes.link} to="/register">Register</Link>
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    signupData: state.signup,
    loginData: state.login,
});

const mapDispatchToProps = {
    login: login,
};

export default connect(mapStateToProps, mapDispatchToProps)(Login);