import * as type from '../types';

const countReducer = (state = 0, action) => {
    switch(action.type) {
        case type.ADD_COUNT:
            return state += action.payload;
        case 'DROP_COUNT':
            return state += action.payload;
            // return state -= 1; //if we dont have payload
        default:
            return state;
    }
}

export default countReducer;
