import * as type from '../types';

const defaultState = () => ({
    isLoading: false,
    data: [],
    error: null,
})

//changing the state(which is set as empty array as default in this case), then the action will change the current state
const todoReducer = (state, action) => {
    if (state === undefined) {
        return defaultState();
    }
    switch(action.type) {
        case type.ADD_TO_DO:
            return {
                isLoading: true,
                data: [...state.data],
                error: null,
            };
            // [...state, action.payload]
        case type.ADD_TO_DO_SUCCESS:
            return{
                isLoading: false,
                data: [...state.data, action.payload],
                error: null,
            }
        case type.ADD_TO_DO_FAIL:
            return {
                isLoading: false,
                data: [...state.data],
                error: action.error,
            }    
        // case type.REMOVE:
        //     console.log(state);
        //     return [...state].filter((ele) => ele.key !== action.payload);
            
        case type.REMOVE:
            // console.log(state);
            return {
                delLoading: true,
                data: [...state.data],
                error: null,
            };
        case type.REMOVE_SUCCESS:
            return{
                delLoading: false,
                data: [...state.data].filter((ele) => ele.key !== action.payload),
                error: null,
            }
        case type.REMOVE_FAIL:
            return {
                delLoading: false,
                data: [...state.data],
                error: action.error,
            }  
        default:
            return state;
    }
}

export default todoReducer;