import * as type from '../types';

export const signup = (data) => (dispatch) => {
    try{
        dispatch ({
            type: type.SIGN_UP,
        });
        setTimeout(() => {
            dispatch({
                type: type.SIGNUP_SUCCESS,
                payload: data,
            })
        }, 3000);
    }
    catch (error){
        dispatch({
            type: type.SIGNUP_FAIL,
            error: error,
        })
    }
    // return {
    //     type: type.SIGN_UP,
    //     payload: data,
    // }
}

