import React from 'react';
import classes from  '../login/login.module.css';
import {connect} from 'react-redux';
import {signup} from '../../actions';
import {Link, Redirect} from 'react-router-dom';

class Signup extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            first: "",
            last: "",
            email: "",
            password: ""
        }
    }
    signup(){
        let first = this._firstname.value;
        let last = this._lastname.value;
        let email = this._email.value;
        let password = this._password.value;
        if(email !== "" || password !== "" || first !== "" || last !== "") {
            const data = {
                first: first,
                last: last,
                email: email,
                password: password
            }
            this.props.signup(data);

            // <Link to="/"></Link> 
            // window.location.href = "http://localhost:3000";
            // this.props.signupData.data.map((check)=> {
            //     if(email !== check.email) {
            //         const data = {
            //             first: first,
            //             last: last,
            //             email: email,
            //             password: password
            //         }
            //         this.props.signup(data);

            //         window.location.href = "http://localhost:3000";
            //     } else {
            //         alert ("This email has been registered");
            //     }
            // })
        } else {
            alert('Please fill in all the details');
        }
    }
    render() {
        return(
            <div className={classes.container}>
                <div className={classes.signup}>
                {this.props.signupData.status == true ? <Redirect to="/" /> : null}
                    <h1 style={{color:`#154c79`}}>Sign Up</h1>
                    <p>Please sign up</p>
                    <input className={classes.input} type="text" id="first" placeholder="first name" ref={(a)=> this._firstname = a} />
                    <input className={classes.input} type="text" id="last" placeholder="last name" ref={(a)=> this._lastname = a} />
                    <input className={classes.input} type="text" id="email" placeholder="email" ref={(a)=> this._email = a} />
                    <input className={classes.input} type="text" id="password" placeholder="password" ref={(a)=> this._password = a}/>
                    <button className={classes.btn} onClick={() => this.signup()}>Register
                        {/* <Link className={classes.link} to="/">Register</Link> */}
                    </button>
                    <button className={classes.btn} >
                        <Link className={classes.link} to="/">Cancel</Link>
                    </button>
                </div>
            </div>
        );
    }
}
const mapStateToProps = (state) => ({
    signupData: state.signup,
    loginData: state.login,
});

const mapDispatchToProps = {
    signup: signup,
};

export default connect(mapStateToProps, mapDispatchToProps)(Signup);